import React, { useEffect } from "react";
import logo from "../logo.svg";
import "../App.css";

function LoadingPage() {
  useEffect(() => {
    console.log("LoadingPage");
  }, []);

  return (
    <div className="App">
      <img src={logo} className="App-logo" alt="logo" />
      <h1>Loading...</h1>
    </div>
  );
}

export default LoadingPage;
